  import urls from '../../utils/urls.js'
  export default {
    data () {
      return {
        lists: [],
        current: 0,
        sliderAnim: {},
				isLogin: this.$store.state.isLogin
      }
    },
    onLoad() {
      // let id = uni.getStorageSync('user').id				
			// let id = JSON.parse(uni.getStorageSync('userInfo')).id
			let id = '';
			if(this.isLogin){
				id = JSON.parse(uni.getStorageSync('userInfo')).id
			}else{
				 id = 0
			}
      uni.request({
        url: urls.HTTP + '/user/formyup',
        method: 'POST',
        data: {
          id: id
        },
        success: (res) => {
          if (res.data.code === 0) {
            this.lists = res.data.userup
          } else {
            this.lists = []
          }
          // console.log('谁赞了我', res)
        }
      })
    },
    onShow (){
      // 创建一个动画实例
    	var animation = uni.createAnimation({
    		duration: 500,
        timingFunction: 'ease'
    	})
      
      this.animation = animation
    },
    methods: {
      changeClass: function (n) {
        this.current = n
        // let id = uni.getStorageSync('user').id
				// let id = JSON.parse(uni.getStorageSync('userInfo')).id
				let id = '';
				if(this.isLogin){
					id = JSON.parse(uni.getStorageSync('userInfo')).id
				}else{
					 id = 0
				}
        var url = '/user/formyup'
        if (n === 0) {
          this.animation.left(0).step()
          this.sliderAnim = this.animation.export()
          url = '/user/formyup'
        } else {
          this.animation.left(100 + '%').step()
          this.sliderAnim = this.animation.export()
          url = '/user/myup'
        }
        uni.request({
          url: urls.HTTP + url,
          method: 'POST',
          data: {
            id: id
          },
          success: (res) => {
            // console.log(url, res)
            if (res.data.code === 0) {
              this.lists = res.data.userup
            } else {
              this.lists = []
              uni.showToast({
                title: res.data.error,
                icon: 'none'
              })
            }
          }
        })
      },
      userLogo: function (img) {
        return urls.UrlHttp + img
      }
    },
    computed: {
      isOne () {
        if (this.current === 0) {
          return true
        } else if (this.current === 1) {
          return false
        }
      }
    }
  }
